@-webkit-keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

@keyframes Gradient {
	0% {
		background-position: 0% 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0% 50%
	}
}

.runeCard {
  border: 0.33em solid black;
  border-radius: 50%;
  background: -webkit-linear-gradient(135deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
  background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
	background-size: 400% 400%;
	-webkit-animation: Gradient 2s ease infinite;
	animation: Gradient 2s ease infinite;

}
.wrapper {
  padding-top: 50px;
  background: url("/assets/images/1-Background.png");
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.title {
  text-align: center;
  font-size: 3rem;
  color:whitesmoke;
  font-family: 'Skranji', cursive;
  padding: 0.25em;
  background: black;
  border-radius:1em;
}

.peripheral{
  font-family: 'Skranji', cursive;

  text-align:center;
  font-size:1rem;
  color:whitesmoke;
  background:black;
  border-radius: 3em;
